@import "../../scss/base";

.paragraphs-item-snp-accordion-menu-item {
  .accordion-trigger {
    display: flex;
    justify-content: space-between;
    align-items: center;

    &:hover,
    &:focus {
      text-decoration: none;
    }

    &.collapsed .accordion-indicator {
      transform: rotate(45deg);
    }

    .accordion-indicator {
      transition: transform 0.3s;
    }
  }
}
